<template>
  <div id="tab">
    <h1 class="tit">tab</h1>
    <div class="tabmenu">
      <ul class="menu">
        <li @click="cur=0" :class="{active:cur==0}"><a href="javascript:;"></a></li>
        <li @click="cur=1" :class="{active:cur==1}"><a href="javascript:;"></a></li>
        <li @click="cur=2" :class="{active:cur==2}"><a href="javascript:;"></a></li>
        <li @click="cur=3" :class="{active:cur==3}"><a href="javascript:;"></a></li>
        <li @click="cur=4" :class="{active:cur==4}"><a href="javascript:;"></a></li>
        <li @click="cur=5" :class="{active:cur==5}"><a href="javascript:;"></a></li>
      </ul>
    </div>
    <div class="tabmain">
      <div class="list" id="list1" v-show="cur==0" >
        
      </div>
      <div class="list" id="list2" v-show="cur==1">
        
      </div>
      <div class="list" id="list3" v-show="cur==2">
        
      </div>
      <div class="list" id="list4" v-show="cur==3">
        
      </div>
      <div class="list" id="list5" v-show="cur==4">
        
      </div>
      <div class="list" id="list6" v-show="cur==5">
        
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'tab',
  data () {
    return {
      show:false,
      cur:0,
    }
  },
  methods: {  
    
  },  
}
</script>

<style scoped>

</style>



